.homepage {
    background-image: url(Background.jpeg);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:top;
    width: 100vw;
    height: 100vh;
    border-radius: 20px;
    box-shadow: 0 0 15px 5px grey;
    position: relative
}

.homepage > .button {
    background: #1877f2;
    border: 1px solid #1877f2;
    color: #fff;
    font-size: 1.25rem;
    padding: 0.5rem;
    margin: 0.5rem 0;
    border-radius: 8px;
    outline: none;
    cursor: pointer;
}

.firstH {
    font-weight: bold;
    color: #fff;
    font-size: 120px;

}

.secondH {
    color: #fff;
    font-size: 62px;
}

